<!--免费课程 -->
<template>
  <div>
    <div>
      <!-- 顶部线条 -->
      <div style="width: 100%;height: 1px;background:#C8C8C8;margin-top: 20px;"></div>
        <div class="grade" :style="gradestyle">
          <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
          <el-submenu index="1">
            <template slot="title">小学</template>
            <el-menu-item index="1-1" class="grade-content">语文</el-menu-item>
            <el-menu-item index="1-2" class="grade-content">数学</el-menu-item>
            <el-menu-item index="1-3" class="grade-content">英语</el-menu-item>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title">初中</template>
            <el-menu-item index="2-1" class="grade-content">语文</el-menu-item>
            <el-menu-item index="2-2" class="grade-content">数学</el-menu-item>
            <el-menu-item index="2-3" class="grade-content">英语</el-menu-item>
          </el-submenu>
          <el-submenu index="3">
            <template slot="title">高中</template>
            <el-menu-item index="3-1" class="grade-content">语文</el-menu-item>
            <el-menu-item index="3-2" class="grade-content">数学</el-menu-item>
            <el-menu-item index="3-3" class="grade-content">英语</el-menu-item>
          </el-submenu>
        </el-menu>
      </div>
      <!-- 课程列表 -->
  <div style="margin: 44px 26px;">
    <el-row>

      <div v-if="type=='freecourse'">
        <el-col class="coursecontent" @click.native="jump('/courseplay')">
          <img v-if="activeIndex=='1-1'" class="courseimg" src="@/assets/img/freecourse/xxyw.jpg">
          <img v-if="activeIndex=='1-2'" class="courseimg" src="@/assets/img/freecourse/xxsx.jpg">
          <img v-if="activeIndex=='1-3'" class="courseimg" src="@/assets/img/freecourse/xxyy.jpg">
          <img v-if="activeIndex=='2-1'" class="courseimg" src="@/assets/img/freecourse/czyw.jpg">
          <img v-if="activeIndex=='2-2'" class="courseimg" src="@/assets/img/freecourse/czsx.jpg">
          <img v-if="activeIndex=='2-3'" class="courseimg" src="@/assets/img/freecourse/czyy.jpg">
          <img v-if="activeIndex=='3-1'" class="courseimg" src="@/assets/img/freecourse/gzyw.jpg">
          <img v-if="activeIndex=='3-2'" class="courseimg" src="@/assets/img/freecourse/gzsx.jpg">
          <img v-if="activeIndex=='3-3'" class="courseimg" src="@/assets/img/freecourse/gzyy.jpg">
        </el-col>
      </div>
      <div v-else>
        <el-col class="coursecontent" @click.native="jump('/coursebuy')">
          <img class="courseimg" src="@/assets/img/freecourse/customized.png">
          <span class="courseprice">¥300</span>
        </el-col>
      </div>

      <el-col style="width: 8%;height: 340px;"></el-col>

      <div v-if="type=='freecourse'">
        <el-col class="coursecontent" @click.native="jump('/courseplay')">
          <img v-if="activeIndex=='1-1'" class="courseimg" src="@/assets/img/freecourse/xxyw.jpg">
          <img v-if="activeIndex=='1-2'" class="courseimg" src="@/assets/img/freecourse/xxsx.jpg">
          <img v-if="activeIndex=='1-3'" class="courseimg" src="@/assets/img/freecourse/xxyy.jpg">
          <img v-if="activeIndex=='2-1'" class="courseimg" src="@/assets/img/freecourse/czyw.jpg">
          <img v-if="activeIndex=='2-2'" class="courseimg" src="@/assets/img/freecourse/czsx.jpg">
          <img v-if="activeIndex=='2-3'" class="courseimg" src="@/assets/img/freecourse/czyy.jpg">
          <img v-if="activeIndex=='3-1'" class="courseimg" src="@/assets/img/freecourse/gzyw.jpg">
          <img v-if="activeIndex=='3-2'" class="courseimg" src="@/assets/img/freecourse/gzsx.jpg">
          <img v-if="activeIndex=='3-3'" class="courseimg" src="@/assets/img/freecourse/gzyy.jpg">
        </el-col>
      </div>
      <div v-else>
        <el-col class="coursecontent" @click.native="jump('/coursebuy')">
          <img class="courseimg" src="@/assets/img/freecourse/customized.png">
          <span class="courseprice">¥300</span>
        </el-col>
      </div>

      <el-col style="width: 8%;height: 340px;"></el-col>

      <div v-if="type=='freecourse'">
        <el-col class="coursecontent" @click.native="jump('/courseplay')">
          <img v-if="activeIndex=='1-1'" class="courseimg" src="@/assets/img/freecourse/xxyw.jpg">
          <img v-if="activeIndex=='1-2'" class="courseimg" src="@/assets/img/freecourse/xxsx.jpg">
          <img v-if="activeIndex=='1-3'" class="courseimg" src="@/assets/img/freecourse/xxyy.jpg">
          <img v-if="activeIndex=='2-1'" class="courseimg" src="@/assets/img/freecourse/czyw.jpg">
          <img v-if="activeIndex=='2-2'" class="courseimg" src="@/assets/img/freecourse/czsx.jpg">
          <img v-if="activeIndex=='2-3'" class="courseimg" src="@/assets/img/freecourse/czyy.jpg">
          <img v-if="activeIndex=='3-1'" class="courseimg" src="@/assets/img/freecourse/gzyw.jpg">
          <img v-if="activeIndex=='3-2'" class="courseimg" src="@/assets/img/freecourse/gzsx.jpg">
          <img v-if="activeIndex=='3-3'" class="courseimg" src="@/assets/img/freecourse/gzyy.jpg">
        </el-col>
      </div>
      <div v-else>
        <el-col class="coursecontent" @click.native="jump('/coursebuy')">
          <img class="courseimg" src="@/assets/img/freecourse/customized.png">
          <span class="courseprice">¥300</span>
        </el-col>
      </div>

    </el-row>
  </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'freecourse-view',
  data() {
    return {
      activeIndex: '1-1',
      gradestyle: '',
      type: '',
    }
  },
  watch: {
    // eslint-disable-next-line no-unused-vars
    $route(to, from) {
      console.info(to);
      this.type = to.path.substr(1);
      if (to.query.activeIndex) {
        this.activeIndex = to.query.activeIndex;
      } else {
        this.activeIndex = '1-1';
      }
    },
  },
  created() {
    this.$emit('header', true, 2);
    this.$emit('footer', true);
    this.gradestyle = `margin-left:${window.innerWidth - 560}px;`;
    this.type = this.$route.path.substr(1);
    if (this.$route.query.activeIndex) {
      this.activeIndex = this.$route.query.activeIndex;
    } else {
      this.activeIndex = '1-1';
    }
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
      this.activeIndex = key;
    },
    jump(routename, params) {
      const routeData = this.$router.resolve({
        name: '',
        path: routename,
        query: params,
        params: {},
      });
      window.open(routeData.href, '_blank');
    },
  },
}
</script>
<style scoped>
  .grade{
    height:52px;
    padding: 30px 0px 0px 0px;
  }
  .grade-content{
    width: 176px;
    text-align: center;
    height: 100px !important;
    line-height: 100px !important;
    color: rgba(16, 16, 16, 1);
    font-size: 30px;
    border:solid 1px #C8C8C8;
    margin: -1px;/* 解决border重叠 */
  }
  .grade>>>.el-menu.el-menu--horizontal {
    border-bottom: none !important;
    margin-top: 15px;
}
  .grade>>>.el-submenu{
    width: 176px;
  }
  .grade>>>.el-submenu__icon-arrow.el-icon-arrow-down{
    display: none;
  }
  .grade>>>.el-submenu__title{
    width: 176px;
    text-align: center;
    line-height: 44px;
    color: rgba(16, 16, 16, 1);
    font-size: 30px;
  }
  .coursecontent{
    width: 28%;
    height: 340px;
    line-height: 20px;
    text-align: center;
    border: 1px solid rgba(187, 187, 187, 1);
  }
  .courseimg{
    width:100%;
    height:100%;
  }
  .courseprice{
    position: relative;
    bottom: 70px;
    left: 33%;
    background: #fff;
    width: 116px;
    height: 54px;
    line-height: 52px;
    color: rgba(255, 152, 0, 1);
    font-size: 36px;
    text-align: center;
    font-family: Roboto;
    border: 1px solid rgba(255, 255, 255, 1);
  }
</style>



